.mainContainerProjects {
    display: flex;
    flex-direction: row;
    width: 95vw !important;
    height: 80vh;
    align-items: center;
    justify-content: center;
    align-self: center;
    border-radius: 0 !important;
    border: 0.1rem solid #252525;
    border-left: none !important;
    border-right: none !important;
}

.projectsScroll {
    opacity: 0;
    animation: projects-Animation;
    animation-duration: 500ms;
    animation-fill-mode: forwards;
    display: flex;
    flex-direction: row;
    overflow-x: scroll;
    width: 100%;
    height: 100%;
    align-items: center;
    scroll-behavior: smooth;

    @media (max-width: 768px) {
        flex-direction: column;
        overflow-y: scroll;
    }

    &::-webkit-scrollbar {
        width: 0px;
        background: transparent;
        /* make scrollbar transparent */
    }

    .project {
        display: grid;
        grid-template-columns: repeat(1, 100%);
        grid-template-rows: repeat(1, 100%);
        margin-left: 3.5rem;
        margin-right: 3.5rem;
        min-width: 60%;
        width: 60%;
        min-height: 75%;
        height: 75%;
        max-height: 75%;
        border: 0.1rem solid #353535;
        border-radius: 1.5rem;
        background-color: #160E10;
        align-items: center;

        @media (max-width: 768px) {
            width: 90%;
            margin: 0;
            margin-top: 5rem;
        }

        .thumbnail:nth-child(1) {
            grid-column: 1 / 1;
            grid-row: 1;
        }

        .thumbnail:nth-child(2) {
            grid-column: 1 / 1;
            grid-row: 1;
            z-index: -1;
            opacity: 0;
            transition: all 250ms ease;
        }

        .thumbnail:nth-child(3) {
            grid-column: 1 / 1;
            grid-row: 1;
            z-index: -2;
            overflow: hidden;
        }

        &:hover {
            .thumbnail:nth-child(2) {
                grid-column: 1 / 1;
                grid-row: 1;
                z-index: 1;
                opacity: 1;
                transition: all 250ms ease;
            }
        }

        .thumbnail {
            min-width: 100%;
            min-height: 100%;
            max-height: 100%;
            display: flex;
            flex-direction: column;
            justify-content: center;
            align-items: center;
            text-align: center;
            filter: brightness(100%);
            transition: all 350ms ease;
            border-radius: 1.5rem;

            &:hover {
                filter: brightness(30%);
                transition: all 350ms ease;
            }

            h2 {
                margin-top: 10rem;
                font-size: 3rem;
            }
        }
    }
}
